<template>
  <div class="QrCode">

  </div>
</template>

<script>
import { wxConfig } from '@/api/index.js';
import wx from 'weixin-js-sdk';
import { queryString } from '@/assets/js/utils.js';

export default {
  name: 'QrCode',
  computed: {},
  data() {
    return {};
  },
  created() {
    // eslint-disable-next-line no-restricted-globals
    const url = location.href;
    wxConfig(url)
      .then((result) => {
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: result.appId, // 必填，公众号的唯一标识
          timestamp: result.timestamp, // 必填，生成签名的时间戳
          nonceStr: result.nonceStr, // 必填，生成签名的随机串
          signature: result.signature, // 必填，签名
          jsApiList: ['scanQRCode'], // 必填，需要使用的JS接口列表
        });
        wx.ready(() => {
          wx.checkJsApi({
            // 判断当前客户端版本是否支持指定JS接口
            jsApiList: ['scanQRCode'],
            success: (res) => {
              if (res.checkResult.scanQRCode === true) {
                wx.scanQRCode({
                  // 微信扫一扫接口
                  desc: 'scanQRCode desc',
                  needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                  scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
                  // eslint-disable-next-line no-shadow
                  success: (res) => {
                    // eslint-disable-next-line no-unused-vars
                    const getCode = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                    const openId = queryString('openId');
                    window.location.href = `${getCode}?openId=${openId}`;
                    console.log(`链接结果${getCode}?openId=${openId}`);
                  },
                });
              } else {
                console.log('抱歉，当前客户端版本不支持扫一扫');
              }
            },
            fail: (res) => {
              // 检测getNetworkType该功能失败时处理
              // eslint-disable-next-line no-alert
              console.log(`isfail${res}`);
            },
          });
        });

        /* 处理失败验证 */
        wx.error((res) => {
          // config 信息验证失败会执行error函数,如签名过期导致验证失败,具体错误信息可以打开config的debug模式查看,也可以在返回的res参数中查看,对于SPA可以在这里更新签名
          console.log(`配置验证失败: ${res.errMsg}`);
        });
      })
      .catch(console.error);
    // eslint-disable-next-line comma-dangle
  }
};
</script>

<style lang="scss" scoped>
.QrCode {
  background: #000;
  height: 100vh;
}
</style>
